import React from 'react'

export default function ErrorAlert() {
    return (
        <div>
             <div className='alertstyle'>
        <div class="alert alert-danger" role="alert">
                <h4 class="alert-heading">Error Has Occured</h4>
                <p>Aww yeah, your error read this important alert message. This example text is going to run a bit longer so that you can see how spacing within an alert works with this kind of content.</p>
                <hr/>
                <p class="mb-0">Whenever you need to, be sure to use margin utilities to keep things nice and tidy.</p>
         </div>
         </div>
        </div>
    )
}
